
*{
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0e80b0;
}


.btn{
  background-color: #fffff;
  color: #e5dc15;
  font-family: 'Monserrat',sans-serif;
  text-transform: uppercase;
  margin: 15px;
  padding: 5px;
  border: 2px salid #e5dc15;
  border-radius: 5px;
  box-shadow: 0 12px #c2bb11, 0 18px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;

}

.disabled .btn{
  cursor: default;
}

.disabled .btn:hover{
  background:#ffff ;
  color: #e5dc15;
  top: 0px;
  box-shadow: 0 12px #c2bb11, 0 18 px rgba(0, 0, 0, 0.4);
}

.btn.disabled{
  cursor: default;
}
.btn:hover{
  background-color: #e5dc15;
  color: #0e80b0;
  top: 4px;
  box-shadow: 0 8px #c2bb11, 0 14px rgba(0, 0, 0, 0.4);

}

.btn:active{
  top: 8px;
  box-shadow: 0 4px #c2bb11, 0 10px rgba(0, 0, 0, 0.4);
}

.game{
  display: flex;
  flex-wrap: wrap;
}
.game-info{
  margin: 15px;
  color: #ffff;
}
.board > div{
  display: flex;
}